@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Prata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya+Sans+SC:ital,wght@1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.Definition-box {
  display: inline-block;
  background-color: rgb(236, 236, 236);
  color: black;
  padding: calc(10px);
  font-family: 'Open Sans', sans-serif;
}

.App-header > .Title {
  display: inline-block;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(40px + 2vmin);
  color: black;
  font-family: 'Prata', serif;
  -webkit-margin-after: 1%;
          margin-block-end: 1%;
}

.App-header > .Subtitle {
  display: block;
  flex-direction: row;
  align-items: left;
  justify-content: start;
  font-size: calc(12px + 2vmin);
  -webkit-margin-before: auto;
          margin-block-start: auto;
  color: rgb(184, 182, 182);
  font-family: 'Alegreya Sans SC', sans-serif;
  font-style: italic;
}

.App-body {
  display: block;
  text-align: start;
  font-family: 'Open Sans', sans-serif;
  margin-top: 2%;
  padding-left: 25%;
  padding-right: 25%;
  padding-bottom: 5%;

}


.quote {
  font-style: italic;
  font-size: calc(10px + 2vmin);
}
.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

